import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Collapse, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, ButtonDropdown, DropdownItem, Nav, NavItem, NavLink, } from 'reactstrap'
import { loadLocalAccount, getShareHost, calcAccountId } from '../js/gegeUtil.js'
import { bshopCall, doBShopTransaction, getTorrentHash } from '../../eshop/eshop'
import classnames from 'classnames';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import AccountDialog from '../../conponents/AccountDialog'
import PopHint from '../../conponents/PopHint/PopHint'
import BtbSecret from '../../conponents/BtbSecret/BtbSecret'
import DepositDialog from '../../conponents/DepositDialog/DepositDialog'
import ReactMapboxGl, { Layer, Feature } from "react-mapbox-gl";
const Map = ReactMapboxGl({
  accessToken: "pk.eyJ1IjoiYnRicmVzb3VyY2UiLCJhIjoiY2pzMnRjZ3N1Mjg2NDQzcXI1MmtkN2JmciJ9.Feg20PaXKCvinpL0nc1Yvw"
});
class NewsList extends Component {

  constructor(props) {
    super(props);
    this.page = 1;
    this.state = {
      list: [], page: 1, delete: false, content: '', longitude: '0', latitude: '0', fee: '0.01',
      deadline: '5', price: '0', gps: false, activeTab: '0', click: false, clickPage: 1, addHongbao: false,
      distance: '0', joinHongbao: false
    }
    this.modules = {
      toolbar: [
        [{ 'header': '1' }, { 'header': '2' }, { 'font': [] }],
        [{ size: [] }], [{ align: [] }, 'direction'],
        ['bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block'],
        [{ color: [] }, { background: [] }],
        [{ 'list': 'ordered' }, { 'list': 'bullet' },
        { 'indent': '-1' }, { 'indent': '+1' }],
        ['link', 'image'],
        ['clean']
      ],
      clipboard: {
        // toggle to add extra line breaks when pasting HTML:
        matchVisual: false,
      }
    }
  }

  componentDidMount() {
    this.loadNewsList(1, this.state.filterIdx)
    var account = loadLocalAccount();
    if (account && account.secret)
      this.secret = account.secret;
    this.setState({ account })
    //this.toggleGps()
  }

  toggleGps() {
    this.setState({ gps: !this.state.gps })
    if (!this.state.gps) {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
          if (position)
            console.log(position.coords.latitude + + position.coords.longitude)
          else {
            console.log('get pos err')
          }
        });
      }
    }
  }

  getFavoriteLocations() {

  }

  preClickPage() {
    if (this.state.clickPage > 1) {
      let clickPage = this.state.clickPage - 1;
      this.setState({ clickPage })
      this.loadClickNews(clickPage)
    }
  }

  nextClickPage() {
    let clickPage = this.state.clickPage + 1;
    this.setState({ clickPage })
    this.loadClickNews(clickPage)
  }

  loadClickNews(page, position) {
    let data;
    let query = this.state.filterValue;
    if (!page)
      page = this.state.clickPage;
    let accountId = this.state.account ? this.state.account.aid : 0;
    let latitude = position ? position.latitude : this.state.position.latitude;
    let longtitude = position ? position.longitude : this.state.position.longitude;
    let zoom = this.map.zoom
    if (query) {
      query = new Buffer(query).toString('base64');
      data = { action: 'listNews', tab: 0, latitude, longtitude, zoom, page, query, accountId }
    } else {
      data = { action: 'listNews', tab: 0, latitude, longtitude, zoom, page, accountId }
    }
    console.log(JSON.stringify(data))
    bshopCall(data, ((ok, list) => {
      if (ok && list) {
        this.setState({ clickList: list.list, clickedHongbaos: list.hongbaos, clickPage: page, filterValue: null })
      }
    }).bind(this))
  }

  loadNewsList(page, query) {
    let data;
    let locations = this.getFavoriteLocations()
    if (!query)
      query = this.state.filterValue;
    if (query) {
      query = new Buffer(query).toString('base64');
      data = { action: 'listNews', locations, page, query }
    } else {
      data = { action: 'listNews', locations, page }
    }
    bshopCall(data, ((ok, list) => {
      if (ok && list) {
        this.setState({ list: list.list })
      }
    }).bind(this))
  }

  loadHongbaoList(page, query) {
    let data;
    let locations = this.getFavoriteLocations()
    if (!query)
      query = this.state.filterValue;
    if (query) {
      query = new Buffer(query).toString('base64');
      data = { action: 'listHongbao', locations, page, query }
    } else {
      data = { action: 'listHongbao', locations, page }
    }
    bshopCall(data, ((ok, list) => {
      if (ok && list) {
        this.setState({ hongbaos: list.list })
      }
    }).bind(this))
  }

  prePage() {
    if (this.state.page > 1) {
      this.state.page--
      this.loadNewsList(this.state.page, this.state.filterIdx, this.state.filterValue)
    }
  }

  nextPage() {
    this.state.page++
    this.loadNewsList(this.state.page, this.state.filterIdx, this.state.filterValue)
  }

  filterBlocks() {
    if (this.state.click) {
      this.loadClickNews(1)
    } else
      this.loadNewsList(this.state.page, this.state.filterIdx, this.state.filterValue)
  }

  renderNewsList() {
    let list = this.state.list;
    if (!list)
      return null;
    let content = list.map((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      let pay = 0;
      if (res.price > 0)
        pay = 1
      return (<tr>
        <td><a target='_blank' href={'/#/newsweb#' + res.id + '#' + pay + res.title}>{res.title}</a></td>
        <td>{res.author}</td>
        <td>距离--公里</td>
        <td>{res.price} BTB</td>
        <td>{btime}</td>
        <td><i className="icon-close icons font-2xl d-block mt-4" onClick={this.toggleDelete.bind(this)}></i></td>
      </tr>)
    });

    return content;
  }


  toggleDelete() {
    this.setState({
      delete: !this.state.delete,
    });
  }

  renderDeleteDialog() {
    return (<Modal isOpen={this.state.delete} toggle={this.toggleDelete.bind(this)}
      className={'modal-danger ' + this.props.className}>
      <ModalHeader toggle={this.toggleDelete.bind(this)}>Download(下载BTB钱包)</ModalHeader>
      <ModalBody>
        <p>Web版钱包不支持朋友圈隱藏功能，隱藏资源功能需要<a href="https://github.com/btbresource/btb-android" target='_blank'>下载APP</a></p>
        <p>Resource hidding is not supported in web wallet.please <a href="https://github.com/btbresource/btb-android" target='_blank'>Download App wallet</a></p>
        <p>彻底隱藏朋友圈需要支付资源价格9999倍的BTB作为手续费(免费资源需要1000个BTB)，手续费将由区块链矿工获得。朋友圈彻底隱藏后将无法查看。</p>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDelete.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }


  renderGPSDialog() {
    return (<Modal isOpen={this.state.gps} toggle={this.toggleGps.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleGps.bind(this)}>需要模糊定位</ModalHeader>
      <ModalBody>
        <p>如果需要通过定位选择周边的朋友圈信息,计算朋友圈的距离，请同意获取模糊位置请求。</p>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleGps.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  toggle(i) {
    const newArray = this.state.dropdownOpen.map((element, index) => { return (index === i ? !element : false); });
    this.setState({
      dropdownOpen: newArray,
    });
  }

  setFilter(idx) {
    this.setState({ page: 1, filterIdx: idx, filterOption: this.filterMsg[idx] })
  }


  upateFilterValue(e) {
    if (e && e.target) {
      let value = e.target.value;
      this.setState({ filterValue: value })
    }
  }

  renderFilterOptions() {
    let ops = this.filterOptions.map((op, index) => {
      return (<DropdownItem onClick={(e) => {
        this.setFilter(index)
      }}>{op}</DropdownItem>)
    })
    return ops;
  }

  renderMap() {
    return (<Card>
      <CardBody>
        <Map
          ref={(m) => this.map = m}
          onClick={(e, coordinates) => {
            let click = coordinates.lngLat;
            let position = { latitude: click.lat, longitude: click.lng }
            this.setState({ position, click: true, clickPage: 1 })
            this.loadClickNews(1, position)
          }}
          zoom={[9]}
          center={[120.45, 40.88]}
          style="mapbox://styles/mapbox/streets-v9"
          containerStyle={{
            height: "100vh",
            width: "90vw"
          }}>
        </Map>
      </CardBody>
      {this.renderClickModal()}
      {this.renderHongbaoDialog()}
    </Card>)
  }

  toggleClick() {
    this.setState({ click: !this.state.click })
  }

  renderClickModal() {
    let pos = this.state.position;
    if (!pos)
      return null
    pos = ' 附近朋友圈[' + pos.longitude + ',' + pos.latitude + ']'
    return (<Modal isOpen={this.state.click} toggle={this.toggleClick.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleClick.bind(this)}>{pos}</ModalHeader>
      <ModalBody>
        {this.renderClickList()}
        <Button onClick={(e) => { this.toggleShareNews() }} className="btn-openid btn-brand"><i className="fa fa-openid"></i><span>Publish(发布这个位置的朋友圈)</span></Button>
        <Button onClick={(e) => { this.toggleShareHongbao() }} className="btn-openid btn-brand"><i className="fa fa-openid"></i><span>Publish(在这个位置发红包)</span></Button>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleClick.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderClickNewsList() {
    let list = this.state.clickList;
    if (!list)
      return null;
    let content = list.map((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      let pay = 0;
      if (res.price > 0)
        pay = 1
      return (<tr>
        <td><a target='_blank' href={'/#/newsweb#' + res.id + '#' + pay + res.title}>{res.title}</a></td>
        <td>{res.author}</td>
        <td>距离--公里</td>
        <td>{btime}</td>
      </tr>)
    });
    return content;

  }

  renderClickList() {
    let list = this.state.clickList;
    let hongbaos = this.state.clickedHongbaos;
    if (!list && !hongbaos)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <InputGroup>
              <Input placeholder='搜索附近朋友圈或红包' onChange={this.upateFilterValue.bind(this)} />
              <Button color="primary" onClick={() => this.filterBlocks()}>
                <i className="fa fa-search"></i>&nbsp;
                </Button>
            </InputGroup>
            <Nav tabs>
              <NavItem>
                <NavLink
                  className={classnames({ active: this.state.clickTab === '0' })}
                  onClick={() => { this.setState({ clickTab: '0' }) }}
                >
                  <i className="icon-calculator"></i> <span className={this.state.clickTab === '0' ? '' : 'd-none'}> All(最近更新) </span>{'\u00A0'}<Badge
                    color="success">附近朋友圈信息</Badge>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={classnames({ active: this.state.clickTab === '1' })}
                  onClick={() => { this.setState({ clickTab: '1' }) }}
                >
                  <i className="icon-calculator"></i> <span className={this.state.clickTab === '1' ? '' : 'd-none'}> All(最近更新) </span>{'\u00A0'}<Badge
                    color="success">附近红包列表 </Badge>
                </NavLink>
              </NavItem>
            </Nav>
            <Table hidden={this.state.clickTab == '1'} hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>Title(标题)</th>
                  <th>Author(发布者)</th>
                  <th>Location(位置)</th>
                  <th>Time(时间)</th>
                </tr>
              </thead>
              <tbody>
                {this.renderClickNewsList()}
              </tbody>
            </Table>
            <Table hidden={this.state.clickTab == '0'} hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>Title(标题)</th>
                  <th>Author(发布者)</th>
                  <th>Location(位置)</th>
                  <th>Amount(总金额)</th>
                  <th>Balance(余额)</th>
                  <th>Limit(距离限制)</th>
                  <th>已经抢购</th>
                  <th>Time(时间)</th>
                  <th>Join(抢红包)</th>
                </tr>
              </thead>
              <tbody>
                {this.renderHongbaoList(this.state.clickedHongbaos)}
              </tbody>
            </Table>

            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preClickPage() }}>Prev(上一页)</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page}</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextClickPage() }}>Next(下一页)</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  toggleJoinHongbao() {
    this.setState({ joinHongbao: !this.state.joinHongbao })
  }

  renderJoinHongbaoDialog() {
    return (<Modal isOpen={this.state.joinHongbao} toggle={this.toggleJoinHongbao.bind(this)}
      className={'modal-danger ' + this.props.className}>
      <ModalHeader toggle={this.toggleJoinHongbao.bind(this)}>下载BTB钱包-抢红包</ModalHeader>
      <ModalBody>
        <p>Web版钱包不支持抢红包功能，抢红包需要<a href="https://github.com/btbresource/btb-android" target='_blank'>下载APP</a></p>
        <p>红包余额大于0的情况下，红包中奖金额随机.</p>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleJoinHongbao.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderHongbaos() {
    let list = this.state.hongbaos;
    if (!list)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <InputGroup>
              <Input placeholder='搜索红包' onChange={this.upateFilterValue.bind(this)} />
              <Button color="primary" onClick={() => this.fitlerHongbao()}>
                <i className="fa fa-search"></i>&nbsp;
                </Button>
            </InputGroup>
            <Table hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>Title(标题)</th>
                  <th>Author(发布者)</th>
                  <th>Location(位置)</th>
                  <th>Amount(总金额)</th>
                  <th>Balance(余额)</th>
                  <th>Limit(距离限制)</th>
                  <th>已经抢购</th>
                  <th>Time(时间)</th>
                  <th>Join(抢红包)</th>
                </tr>
              </thead>
              <tbody>
                {this.renderHongbaoList(this.state.hongbaos)}
                {this.renderJoinHongbaoDialog()}
                {this.renderHongbaoDialog()}
              </tbody>
            </Table>
            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>Prev(上一页)</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page}</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>Next(下一页)</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  showHongbao(hongbao) {
    this.setState({ hongbao, showHongbao: true })
    this.loadHongbaoInfo(hongbao)
  }

  loadHongbaoInfo(hb) {
    let data = { action: 'hongbao', id: ('' + hb.id) }
    bshopCall(data, ((ok, hb) => {
      if (ok && hb) {
        let hongbao = this.state.hongbao;
        hongbao.joins = hb.list;
        hongbao.content = hb.content;
        this.setState({ hongbao })
      }
    }).bind(this))
  }

  renderHongbaoJoins() {
    if (!this.state.hongbao || !this.state.hongbao.joins)
      return null;
    let records = this.state.hongbao.joins.map((r) => {
      let d = new Date();
      d.setTime(r.time)
      let btime = d.toLocaleString()
      return (<tr>
        <td><AccountDialog rsId={r.senderId} /></td>
        <td>{r.amount}</td>
        <td>{btime}</td>
      </tr>)
    })
    return (<Table hover bordered striped responsive size="sm">
      <thead>
        <tr>
          <th>Author(账户)</th>
          <th>Amount(获奖金额)</th>
          <th>Time(时间)</th>
        </tr>
      </thead>
      <tbody>
        {records}
      </tbody>
    </Table>)
  }

  toggleHongbaoInfo() {
    this.setState({ showHongbao: !this.state.showHongbao })
  }

  renderHongbaoDialog() {
    let hb = this.state.hongbao;
    if (!hb)
      return null
    let content = '抢红包....';
    if (hb.content) {
      content = hb.content;
    }
    return (<Modal isOpen={this.state.showHongbao} toggle={this.toggleHongbaoInfo.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleHongbaoInfo.bind(this)}>{hb.title}</ModalHeader>
      <ModalBody>
        <ReactQuill value={content} />
        {this.renderHongbaoJoins()}
      </ModalBody>
      <ModalFooter>
        <Button color="info" onClick={this.toggleDelete.bind(this)}>下载客户端APP抢红包</Button>
        <Button color="info" onClick={this.toggleHongbaoInfo.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderHongbaoList(list) {
    if (!list)
      return null;
    let content = list.map((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()

      let pos = '[' + res.latitude + ',' + res.longitude + ']'
      return (<tr>
        <td><a href="#" onClick={(e) => {
          e.preventDefault()
          this.showHongbao(res)
        }}>{res.title}</a></td>
        <td><AccountDialog rsId={res.sender} /></td>
        <td>{pos}</td>
        <td>{res.amount} BTB</td>
        <td>{res.balance} BTB</td>
        <td>{res.distance}公里</td>
        <td>{res.joinCount}次</td>
        <td>{btime}</td>
        <td><i className="icon-close icons font-2xl d-block mt-4" onClick={this.toggleJoinHongbao.bind(this)}></i></td>
      </tr>)
    });

    return content;
  }


  renderList() {
    let list = this.state.list;
    if (!list)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <InputGroup>
              <Input placeholder='搜索所有朋友圈' onChange={this.upateFilterValue.bind(this)} />
              <Button color="primary" onClick={() => this.filterBlocks()}>
                <i className="fa fa-search"></i>&nbsp;
                </Button>
            </InputGroup>
            <Table hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>Title(标题)</th>
                  <th>Author(发布者)</th>
                  <th>Location(位置)</th>
                  <th>Price(价格)</th>
                  <th>Time(时间)</th>
                  <th>Hide(删除)</th>
                </tr>
              </thead>
              <tbody>
                {this.renderNewsList()}
              </tbody>
            </Table>
            {this.renderGPSDialog()}
            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>Prev(上一页)</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page}</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>Next(下一页)</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  onSecret(sec) {
    if (sec) {
      this.secret = sec
      let rsId = calcAccountId(this.userSec);
      this.setState({ rsId: rsId })
    }
  }

  updateFee(e) {
    if (e && e.target) {
      let fee = e.target.value;

      if (this.fee.length == 0) {
        this.fee = 1;
      }
    }
  }
  updateDeadline(e) {
    if (e && e.target) {
      let deadline = e.target.value;
      if (deadline.length == 0) {
        this.setState({ deadline: '5' })
      } else {
        this.setState({ deadline })
      }
    }
  }
  updateTitle(e) {
    if (e && e.target) {
      let title = e.target.value;
      this.setState({ title })
    }
  }

  updateDistance(e) {
    if (e && e.target) {
      let distance = e.target.value;
      this.setState({ distance })
    }
  }

  handleChange(value) {
    this.setState({ content: value })
  }

  updatePrice(e) {
    if (e && e.target) {
      let price = e.target.value;
      this.setState({ price })
    }
  }

  renderdialogMsg() {
    if (this.state.renderDesposit) {
      return (<div>
        <Label>{this.state.dialogMsg}</Label>
        <DepositDialog />
      </div>)
    } else
      if (this.state.dialogMsg) {
        return (<Label>{this.state.dialogMsg}</Label>)
      }
    return null;
  }
  renderHongbaoPosition() {
    let position = this.state.position
    let longtitude = position ? position.longitude : this.state.longitude;
    let latitude = position ? position.latitude : this.state.latitude;
    let pos = '[' + longtitude + ',' + latitude + ']'
    return (<FormGroup>
      <div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText><PopHint id='hb2323' text='(红包位置)Geo:' content='这个位置附近的用户查看朋友圈时会看到红包信息。只有在这个位置附近发布过朋友圈信息的用户才可以参与抢红包。'></PopHint></InputGroupText>
          </InputGroupAddon>
          <Label>{pos}</Label>
        </InputGroup>
      </div>
    </FormGroup>)
  }

  renderNewsPosition() {
    let position = this.state.position
    let longtitude = position ? position.longitude : this.state.longitude;
    let latitude = position ? position.latitude : this.state.latitude;
    let pos = '[' + longtitude + ',' + latitude + ']'
    return (<FormGroup>
      <div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText><PopHint id='hb23222' text='(朋友圈位置)Geo:' content='朋友圈信息位置经纬度。附近位置的其他用户在地图上点击时可以看到。在这个位置附近发布过朋友圈信息的用户才有资格参与此位置附近的抢红包活动。' ></PopHint></InputGroupText>
          </InputGroupAddon>
          <Label>{pos}</Label>
        </InputGroup>
      </div>
    </FormGroup>)
  }

  rederAddHongbaoDialog() {
    return (<Modal isOpen={this.state.addHongbao} toggle={this.toggleShareHongbao.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleShareHongbao.bind(this)}>Publish(发红包)</ModalHeader>
      <ModalBody>
        <FormGroup>
          <div className="controls">
            <BtbSecret onSecret={this.onSecret.bind(this)} />
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>(红包标题)Title:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTitle.bind(this)} placeholder='Title 标题' />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderHongbaoPosition()}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='pri4545' text='Limit(距离限制):' content='限制在这个位置周边限制距离之内发布过朋友圈的用户才可以抢红包。0则没有限制。数值加1则距离增加10公里' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updateDistance.bind(this)} placeholder='0' />
              <InputGroupAddon addonType="append">
                <InputGroupText>x 10公里</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='pri4545' text='Amount(金额):' content='红包的总金额.最小10个。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updatePrice.bind(this)} placeholder='0' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='Fee(矿工手续费):' content='区块链矿工为账户余额比较多的BTB账户随机产生，负责验证所有交易是否符合规则,并将合法交易保存到区块链。矿工保证整个系统按照规则运行。同时获得交易手续费。发布红包最少0.01个' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" placeholder='0.01' type="text" onChange={this.updateFee.bind(this)} />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='dl4545' text='Deadline(有效期):' content='矿工验证交易时候会按手续费高低排序处理，有效期内未被处理的交易会被舍弃。交易作废。默认5分钟。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" placeholder='5' onChange={this.updateDeadline.bind(this)} type="text" />
              <InputGroupAddon addonType="append">
                <InputGroupText>minutes(分钟)</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Details(红包内容):</Label>
          <ReactQuill value={this.state.content}
            modules={this.modules}
            placeholder='这里编辑其他人抢红包时可以看到的内容..'
            onChange={this.handleChange.bind(this)} />
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleShareHongbao.bind(this)}>Close(关闭)</Button>
        <Button color="danger" hidden={this.state.success} onClick={
          ((e) => {
            let dialogMsg = "Working...(红包发布中,有图片可能需要几分钟)";
            this.setState({ dialogMsg })
            setTimeout(() => {
              this.doSendHongbao()
            }, 200);
          }).bind(this)
        }>Hongbao(发红包)</Button>
      </ModalFooter>
    </Modal>)
  }

  rederAddProductDialog() {
    return (<Modal isOpen={this.state.addProduct} toggle={this.toggleShareNews.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleShareNews.bind(this)}>Publish(发布朋友圈)</ModalHeader>
      <ModalBody>
        <FormGroup>
          <div className="controls">
            <BtbSecret onSecret={this.onSecret.bind(this)} />
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>(朋友圈标题)Title:</InputGroupText>
              </InputGroupAddon>
              <Input id="prependedInput2" size="16" type="text" onChange={this.updateTitle.bind(this)} placeholder='Title 标题' />
            </InputGroup>
          </div>
        </FormGroup>
        {this.renderNewsPosition()}
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='pri4545' text='Price(价格):' content='价格等于0的朋友圈所有人都可以查看。价格大于0的朋友圈信息需要付费给发布者之后才能查看具体内容。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updatePrice.bind(this)} placeholder='0' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>

        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='Fee(矿工手续费):' content='区块链矿工为账户余额比较多的BTB账户随机产生，负责验证所有交易是否符合规则,并将合法交易保存到区块链。矿工保证整个系统按照规则运行。同时获得交易手续费。发布朋友圈最少0.01个' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" placeholder='0.01' type="text" onChange={this.updateFee.bind(this)} />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id='dl4545' text='Deadline(有效期):' content='矿工验证交易时候会按手续费高低排序处理，有效期内未被处理的交易会被舍弃。交易作废。默认5分钟。' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" placeholder='5' onChange={this.updateDeadline.bind(this)} type="text" />
              <InputGroupAddon addonType="append">
                <InputGroupText>minutes(分钟)</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Details(详细内容):</Label>
          <ReactQuill value={this.state.content}
            modules={this.modules}
            placeholder='这里编辑朋友圈内容，添加图片等...'
            onChange={this.handleChange.bind(this)} />
        </FormGroup>
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleShareNews.bind(this)}>Close(关闭)</Button>
        <Button color="danger" hidden={this.state.success} onClick={
          ((e) => {
            let dialogMsg = "Working...(发布中,有图片可能需要几分钟)";
            this.setState({ dialogMsg })
            setTimeout(() => {
              this.dopublishNews()
            }, 200);
          }).bind(this)
        }>Publish(发布)</Button>
      </ModalFooter>
    </Modal>)
  }

  uploadNews(newsId, content) {
    console.log('upload news not done yet');
    const get = require('simple-get')
    localStorage.setItem(newsId, content);
    let b64 = Buffer.from(content).toString('base64');
    let url = 'http://upload.btbresource.com:18080/upload/' + newsId + '/token1'
    //url = 'http://127.0.0.1:18080/info'
    var opts = {
      url: url,
      body: b64,
      json: false
    };
    get.concat(opts, function (err, res, data) {

    });
  }

  doSendHongbao() {
    if (!this.state.title) {
      this.setState({ dialogMsg: '缺少红包标题' })
      return;
    }
    let content = this.state.content
    content = '<html><head><meta charset="UTF-8"></head><p>' + content + '</p></html>';
    content = Buffer.from(content).toString('base64')
    let title = Buffer.from(this.state.title).toString('base64');
    let position = this.state.position
    let longtitude = (position ? position.longitude : this.state.longitude) + '';
    let latitude = (position ? position.latitude : this.state.latitude) + '';
    let distance = this.state.distance;
    let data = {
      action: 'transaction', type: 'hongbao', content, accountId: this.state.account.aid, fee: this.state.fee, price: this.state.price, deadline: this.state.deadline,
      tags: 'no', title, longtitude, distance, latitude
    }
    doBShopTransaction(this.state.account.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        this.setState({ dialogMsg: '红包发布成功,矿工验证后(大约一分钟)即可显示.', success: true })
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '发布失败' })
      }
    }).bind(this))
  }

  dopublishNews() {
    if (!this.state.title) {
      this.setState({ dialogMsg: '缺少朋友圈标题' })
      return;
    }
    let content = this.state.content
    content = '<html><head><meta charset="UTF-8"></head><p>' + content + '</p></html>';
    let newsId = getTorrentHash(content);
    let title = Buffer.from(this.state.title).toString('base64');
    let position = this.state.position
    let longtitude = (position ? position.longitude : this.state.longitude) + '';
    let latitude = (position ? position.latitude : this.state.latitude) + '';
    let data = {
      action: 'transaction', type: 'news', newsId, accountId: this.state.account.aid, fee: this.state.fee, price: this.state.price, deadline: this.state.deadline,
      tags: 'no', title, longtitude, latitude
    }
    doBShopTransaction(this.state.account.secret, data, ((ok, msg) => {
      if (ok && msg && msg.ok) {
        this.setState({ dialogMsg: '发布成功,矿工验证后(大约一分钟)即可显示.', success: true })
        //upload to server 
        this.uploadNews(newsId, content)
      } else {
        if (msg && !msg.ok && '1001' === msg.code) {
          this.setState({ dialogMsg: '账户余额不足。', renderDesposit: true })
        } else
          this.setState({ dialogMsg: '发布失败' })
      }
    }).bind(this))
  }

  toggleShareNews() {
    this.setState({
      addProduct: !this.state.addProduct, success: false
    });
  }

  toggleShareHongbao() {
    this.setState({
      addHongbao: !this.state.addHongbao, success: false
    });
  }

  onTabClicked(activeTab) {
    this.setState({ activeTab })
    if (activeTab === '2') {
      this.loadHongbaoList()
    }
  }


  renderContent() {
    if (this.state.activeTab == '0')
      return this.renderList()
    else if (this.state.activeTab == '2')
      return this.renderHongbaos()
    else
      return this.renderMap();
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '0' })}
              onClick={() => { this.onTabClicked('0'); }}
            >
              <i className="icon-calculator"></i> <span className={this.state.activeTab === '0' ? '' : 'd-none'}> All(最近更新) </span>{'\u00A0'}<Badge
                color="success">Latest(所有朋友圈信息) </Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.onTabClicked('2'); }}
            >
              <i className="icon-calculator"></i> <span className={this.state.activeTab === '2' ? '' : 'd-none'}>HongBao(附近红包) </span>{'\u00A0'}<Badge
                color="success">Latest(所有红包列表) </Badge>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1' })}
              onClick={() => { this.onTabClicked('1'); }}
            >
              <i className="icon-basket-loaded"></i> <span
                className={this.state.activeTab === '1' ? '' : 'd-none'}>Click(点击地图显示附近朋友圈和红包信息)</span>{'\u00A0'}<Badge pill color="danger">Map(地图模式)</Badge>
            </NavLink>
          </NavItem>
        </Nav>
        {this.renderContent()}
        {this.rederAddProductDialog()}
        {this.rederAddHongbaoDialog()}
        {this.renderDeleteDialog()}
        <Button onClick={(e) => { this.toggleShareNews() }} className="btn-openid btn-brand"><i className="fa fa-openid"></i><span>Publish(发布朋友圈信息)</span></Button>
      </div>
    );
  }
}

export default NewsList;



